import './App.scss';
import Swal from 'sweetalert2';
import withReactContent from 'sweetalert2-react-content';
import logo from './assets/img/logo.png'
import placeholder from './assets/img/placeholder.png'
import { useState } from 'react';

const sampleItems = [
  { 
    name: "Sample Item 00", 
    price: 149.95, 
    description: "A versatile and stylish item for everyday use. Its sleek design ensures it fits seamlessly into any setting, from home to office. Built with durable materials, it offers both longevity and reliability." 
  },
  { 
    name: "Sample Item 01", 
    price: 249.95, 
    description: "High-quality and durable for long-lasting performance. This item is engineered to handle demanding tasks with ease. Whether for personal or professional use, it delivers consistent results every time." 
  },
  { 
    name: "Sample Item 02", 
    price: 329.95, 
    description: "Perfect for those who value functionality and elegance. Its refined aesthetic is complemented by innovative features that enhance user experience. Enjoy the perfect balance of practicality and sophistication." 
  },
  { 
    name: "Sample Item 03", 
    price: 99.95, 
    description: "A budget-friendly option with great features. Despite its affordable price, it doesnt compromise on quality or performance. This item is an excellent choice for those seeking value without sacrificing functionality." 
  },
  { 
    name: "Sample Item 04", 
    price: 459.95, 
    description: "Designed for professionals who demand excellence. Every detail has been carefully crafted to deliver superior performance in even the most challenging environments. This item sets a new standard for quality and innovation." 
  },
  { 
    name: "Sample Item 05", 
    price: 199.95, 
    description: "Combines modern design with outstanding usability. Its intuitive features make it easy to use while maintaining a sleek, contemporary look. Perfect for anyone seeking a reliable yet stylish addition to their collection." 
  },
  { 
    name: "Sample Item 06", 
    price: 399.95, 
    description: "An innovative product built to exceed expectations. This item integrates cutting-edge technology with a user-friendly interface. Its ideal for those who appreciate products that are both advanced and accessible." 
  },
  { 
    name: "Sample Item 07", 
    price: 74.95, 
    description: "Compact and lightweight, ideal for travel or home. Despite its small size, it offers robust features and excellent performance. This item is the perfect companion for those on the go or with limited space." 
  }
];


const ProductPreview = ({data}) => {
  if (data) {
    return <div className=''>
      <div className='flex flex-wrap md:flex-nowrap gap-4'>
        <div className='md:basis-1/2 w-full'>
          <img src={placeholder} />
        </div>
        <div className='md:basis-1/2 w-full'>
          <div className='flex flex-wrap w-full h-full'>
            <div className='grow p-8'>
              <div className='text-right text-4xl pb-2'>{data.name}</div>
              <div className='text-right font-semibold pb-2'><span className='text-xl'>$</span><span className='text-3xl'>{data.price}</span></div>
              <div className='w-full text-justify'>{data.description}</div>
            </div>
            <div className='w-full grow shrink-0 mt-auto p-4'>
              <button className='w-full px-4 py-4 border-[3px] border-[#101010] bg-[#101010] text-white font-semibold hover:bg-white hover:text-[#101010] active:scale-[0.99] duration-100 active:duration-0'>Purchase</button>
            </div>
          </div>
        </div>
      </div>
    </div>
  }
}

const ProductCard = ({ data }) => {

  const onClick = (data) => {
    withReactContent(Swal).fire({
      html: <ProductPreview data={data}/>,
      showConfirmButton: false,
      animation: false,
      customClass: {
        popup: 'w-full max-w-5xl p-0 rounded-none',
        htmlContainer: 'p-0',
        container: 'p-8'
      }
    })
  }

  return <div className='col-span-6 sm:col-span-4 md:col-span-3 cursor-pointer hover:outline hover:outline-[#101010] hover:-translate-y-1 duration-100' onClick={ () => onClick(data) }>
    <div>
      <img src={placeholder} />
    </div>
    <div className='p-4 border border-[#e0e0e0] '>
      <div>
        <span className='text-lg'>{ data.name }</span>
      </div>
      <div>
        <span className='font-bold text-md'>$</span><span className='font-bold text-xl'>{ data.price }</span>
      </div>
    </div>
  </div>
}

const renderProducts = (items) => {
  return sampleItems.map((item, i) => <ProductCard key={ i } data={ item } />)
}

const App = () => {
  return (
    <div className='app app-single-page-commerce'>
      <div className='max-w-7xl mx-auto'>
        <div className='p-4'>
          <div className='w-full max-w-32'>
            <img src={logo} />
          </div>
        </div>
        <div className='p-4'>
          <div className='grid grid-cols-12 gap-4'>
            { renderProducts(sampleItems) }
          </div>
        </div>
        <footer>
          <div className='p-4 text-center'>
             2024{ new Date().getFullYear() !== 2024 && '-' + new Date().getFullYear() } ZGI. All Rights Reserved.
          </div>
        </footer>
      </div>
    </div>
  );
}

export default App;
